<template lang="pug">
section.hero.is-fullheight.scroll-here(
  style="background: linear-gradient(#111, #7957d5) !important; padding-top: 96px"
)
  .hero-header(
    v-waypoint="{ active: true, callback: aboutVisible, options: intersectionOptions }"
  )
    .container.has-text-centered.is-flex-direction-column.is-justify-content-center
      h1.title.primary.is-uppercase.about About
      hr.primary(style="background-color: #fab")
  .hero-body(style="padding-top: 8px")
    .tile.is-ancestor.is-horizontal
      .tile.is-parent.is-6
        .columns.is-mobile.is-flex-wrap-wrap(style="padding-bottom: 96px")
          .tile.column.is-6(v-for="(item, index) in items")
            .tile.is-parent(style="height: 100%")
              .tile.is-child.box.has-background-dark.hoverable.primary(
                style="height: 100%"
              )
                .container.has-text-centered
                  .hex-wrap
                    .hexagon(
                      @click="modal = index",
                      style="background-color: #a49"
                    )
                      b-icon.top(:icon="item.icon", custom-size="mdi-48px")
                  h1.is-size-5-desktop.is-size-7-mobile.has-text-weight-bold.is-family-sans-serif.is-capitalized {{ item.title }}
                  h3.is-size-6-desktop.is-size-7-mobile.has-text-weight-normal.is-family-secondary {{ item.summary }}
      .tile.is-parent.is-6(
        style="padding-top: 32px",
        v-waypoint="{ active: true, callback: progressBarsVisible, options: intersectionOptions }"
      )
        .container.is-flex-column.is-justify-content-center
          .container(style="width: 50%")
            b-progress(
              show-value,
              v-for="skill in skills",
              :value="skill.current"
            ) 
              span(
                style="color: #f9d; text-shadow: 2px 2px 8px #334; background: #444"
              ) {{ skill.name }} ({{ skill.value }})
</template>

<script>
export default {
  methods: {
    aboutVisible({ going, direction }) {
      if (going !== "in" || this.aboutShown) return;
      this.aboutShown = true;
      const el = this.$el.querySelector("h1.title.primary.is-uppercase.about");
      if (el != null) {
        el.classList.add("visible");
      }
    },
    progressBarsVisible({ going, direction }) {
      if (going !== "in" || this.barsShown) return;
      this.barsShown = true;
      let done = 0;
      let callbacks = [];
      this.skills.forEach((skill) => {
        callbacks.push(() => {
          skill.current += (skill.value - skill.current) * 0.25;
          if (Math.abs(skill.current - skill.value) <= 0.5) {
            skill.current = skill.value;
            done++;
          }
        });
      });
      let interval = setInterval(() => {
        callbacks.forEach((callback) => callback());
        if (done == callbacks.length) clearInterval();
      }, 0.5);
    },
  },
  data() {
    return {
      barsShown: false,
      aboutShown: false,
      modal: null,
      items: [
        {
          icon: "cellphone-link",
          title: "Responsive",
          summary:
            "Layouts that scale, while maintaining aesthetic for all devices, small and large",
        },
        {
          icon: "speedometer",
          title: "Quick",
          summary:
            "Web pages that leverage JavaScript and SPA design principles to eliminate lengthy page load times",
        },
        {
          icon: "database-check",
          title: "Data-driven",
          summary:
            "Designs that evolve to best fit the domain's data models and business objects.",
        },
        {
          icon: "account-check",
          title: "User-friendly",
          summary: "Interfaces that are minimal, and intuitive in design.",
        },
      ],
      skills: [
        {
          name: "JavaScript",
          value: 90,
        },
        {
          name: "Node.js",
          value: 90,
        },
        {
          name: "Haxe",
          value: 90,
        },
        {
          name: "Vue.js (2.0)",
          value: 80,
        },
        {
          name: "Back-End",
          value: 75,
        },

        {
          name: "C#",
          value: 75,
        },
        {
          name: "SQL",
          value: 60,
        },
        {
          name: "Front-End",
          value: 50,
        },
        {
          name: "Python",
          value: 50,
        },
        {
          name: "HTML",
          value: 50,
        },
        {
          name: "CSS",
          value: 50,
        },
        {
          name: "Rust",
          value: 25,
        },
      ].map((entry) => {
        entry.current = 0;
        return entry;
      }),
    };
  },
};
</script>

<style lang="sass" scoped>
.about
  transition: all 0.5s
  transform: translateX(-100vw)
  opacity: 0
  &.visible
    transition: all 0.5s
    transform: none !important
    opacity: 1
.header-bar
  background: #444649
  height: 4px
  margin: 25px 0 75px 0
  width: 70px
.primary
  color: #f98 !important
</style>