<template lang="pug">
#home(v-scroll-spy="{data: 'section'}", sticky-container)
  .scroll-here
    .hero.is-fullheight
      .hero-body
        particles-bg(
          type="cobweb",
          :canvas="{ background: 'linear-gradient(#777, #111)' }",
          :bg="true"
        )
        .container.has-text-centered
          .is-flex-wrap-wrap.is-align-items-center
            h1.title.has-text-light.is-family-sans-serif.glow Gabriel Hayes
            h2.subtitle.glow.is-family-sans-serif.has-text-weight-light The Progressive Web Developer
            b-button(@click="scrollTo('about')", type="is-light", outlined)
              span View my work
              b-icon(icon="arrow-right-thick", custom-size="mdi-12px")
    .hero-footer(v-sticky, style="background: #111")
      .hero-footer
        nav.tabs
          .container.is-fullhd
            ul(v-scroll-spy-active="{class: 'is-active'}", v-scroll-spy-link)
              li(v-for="link in links")
                a.has-text-light(@click.prevent.stop="scrollTo(link.location)") {{ link.text }}

  About#about
  //- Portfolio#portfolio
  Contact#contact
</template>

<script>
import Introduction from "@/components/Introduction.vue";
import About from "@/components/About.vue";
import Portfolio from "@/components/Portfolio.vue";
import Contact from "@/components/Contact.vue";
// @ is an alias to /src
export default {
  name: "Home",
  components: {
    Introduction,
    Portfolio,
    About,
    Contact,
  },
  methods: {
    scrollTo(location, evt) {
      if (evt) {
        console.log(evt);
        alert(evt);
        evt.stopPropagation();
        evt.preventDefault();
      }
      const el = document.getElementById(location);
      if (el) {
        setTimeout(() => {
          window.scrollTo({
            top: el.offsetTop + 48,
            behavior: "smooth",
          });
        }, 0);
      }
    },
  },
  data() {
    return {
      section: 0,
      links: [
        {
          location: "#home",
          text: "Home",
        },
        {
          location: "#about",
          text: "About",
        },
        // {
        //   location: "#portfolio",
        //   text: "Portfolio",
        // },
        {
          location: "#contact",
          text: "Contact",
        },
      ],
    };
  },
};
</script>

