<template lang="pug">
div(style="background: #223")
  svg.svgcolor-light(
    preserveAspectRatio="none",
    viewBox="0 0 100 102",
    height="75",
    width="100%",
    version="1.1",
    xmlns="http://www.w3.org/2000/svg"
  )
    path(d="M0 0 L50 100 L100 0 Z", fill="#7957d5", stroke="#7957d5")

  section.hero.is-fullheight.scroll-here(
    style="background: #223 !important; padding-top: 96px"
  )
    .hero-header(
      v-waypoint="{ active: true, callback: contactVisible, options: intersectionOptions }"
    )
      .container.has-text-centered.is-flex-direction-column.is-justify-content-center
        h1.title.primary.is-uppercase.contact Contact
        hr.primary(style="background-color: #7957d5")
    .hero-body(style="padding-top: 8px")
      .container.has-text-centered
        h2.subtitle.primary Looking for a developer or consultant?
        h3.subtitle.is-size-6(style="color: #aab") Leave a message below
        form
          b-field 
            b-input.dark-input(
              v-model="name",
              placeholder="Name",
              icon="account",
              required
            )
          //- b-field 
            b-input.dark-input(v-model="email", placeholder="E-Mail", icon="email")
          b-field 
            b-input.dark-input(
              v-model="message",
              placeholder="Message",
              type="textarea",
              minlength="16",
              maxlength="256",
              required
            )
          .is-flex-direction-row.justify-content-end
            b-button(
              type="is-dark",
              native-type="submit",
              :disabled="FormInvalid",
              @click="sendMessage"
            ) Send
    .hero-footer(style="background-color: #111; height: 256px !important")
      .container.has-text-centered.is-flex.is-flex-direction-row.is-justify-content-space-between.is-align-items-center(
        style="height: 100%; padding: 48px"
      )
        b-tooltip(:label="item.tip", v-for="item in items", type="is-dark")
          .icon-wrap.is-flex.is-flex-direction-column.is-align-items-center.is-justify-content-center(
            @click="openLink(item.href)"
          )
            b-icon.icon(:icon="item.icon", custom-size="mdi-32px")
</template>

<script>
const axios = require("axios");
export default {
  computed: {
    FormInvalid() {
      return (
        this.message.length < 16 || this.message.length > 256 || !this.name
      );
    },
  },
  methods: {
    openLink(href) {
      window.open(href, "_blank");
    },
    sendMessage() {
      this.$gAuth.signIn().then((user) => {
        const profile = user.getBasicProfile();
        const email = profile.cu;
        const googleName = profile.Ad;
        axios
          .post(
            "https://us-central1-progressive-web-dev-300511.cloudfunctions.net/sendEmail",
            {
              name: `${this.name} (${googleName})`,
              email,
              message: this.message,
            }
          )
          .then((res) => {
            this.$buefy.toast.open({
              message: res.data,
              type: "is-success",
            });
          })
          .catch((err) => {
            this.$buefy.toast.open({
              message: "Whoops! Please try again later",
              type: "is-danger",
            });
          });
      });
    },
    contactVisible({ going, direction }) {
      if (going !== "in" || this.contactShown) return;
      this.contactShown = true;
      const el = this.$el.querySelector(
        "h1.title.primary.is-uppercase.contact"
      );
      if (el) {
        el.classList.add("visible");
      }
    },
  },
  data() {
    return {
      contactShown: false,
      name: "",
      email: "",
      message: "",
      items: [
        {
          icon: "linkedin",
          href: "https://www.linkedin.com/in/gabriel-hayes-b79b28144/",
          tip: "Connect with me",
        },
        {
          icon: "stack-overflow",
          href: "https://stackoverflow.com/users/1538301/gabriel-hayes",
          tip: "Knowledge check",
        },
        {
          icon: "github",
          href: "https://github.com/piboistudios",
          tip: "Mad scientist things",
        },
      ],
    };
  },
};
</script>
<style lang="sass" scoped>
.icon-wrap
  background: #223
  cursor: pointer
  font-size: 18pt
  height: 55px
  margin: 0 15px
  overflow: hidden
  position: relative
  text-align: center
  transition: background 0.3s, color 0.3s, box-shadow 0.3s, transform 0.3s
  width: 55px
  color: #aac
  &:hover
    background: #bbc
    color: #334
    box-shadow: 0 0 0 3px rgba(4, 94, 201, 0.1)
    transform: scale(0.9)
    .icon
      animation: onHover 0.3s forwards
.slant::before
  content: ""
  width: 100%
  height: 128px
  background: #111
  position: relative
  top: -80px
  transform: rotate(6deg) translateX(-32px)
.contact
  transition: all 0.5s
  transform: translateX(-100vw)
  opacity: 0
  &.visible
    transition: all 0.5s
    transform: none !important
    opacity: 1
.primary
  color: #7957d5

@keyframes onHover
  49%
    transform: translateY(100%)

  50%
    opacity: 0
    transform: translateY(-100%)

  51%
    opacity: 1
</style>